@import "mixins";

header.main{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 115px;
  background: var(--color-transparent);
  z-index: 1000;
  @include mob {
    height: 75px;
  }
  &.bg{
    background: var(--color-white);
  }
  &.hidden{
    top: -130px;

    @include mob {
      top: 0;
    }
  }
  .wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    @include mob {
      position: relative;
    }
    a.logo{
      display: block;
      width: 160px;
      height: 55px;
      @include mob {
        width: auto;
        height: auto;
      }
      img{
        display: block;
        width: 100%;

        @include mob {
          width: 99px;
          height: 33px;
          object-fit:cover;
          object-position: 0 center;
          transition: all 0.2s linear;
        }
      }
    }
    ul{
      display: flex;
      align-items: center;
      &.nav{
        column-gap: 30px;
        @include mob {
          position: absolute;
          top: 75px;
          left: 0;
          width: 100%;
          min-height: calc(100vh - 75px);
          background: #F5F5F5;

          flex-flow: column nowrap;
          justify-content: flex-start;
          align-items: flex-start;
          padding: 63px 16px 30px;

          transform: translateX(-100%);
          transition: transform 0.2s linear;
        }
        li{
          position: relative;
          @include mob {
            margin-bottom: 20px;
            &:last-child{
              margin-bottom: 0;
            }
          }
          &.mob-only{
            display: none;
            @include mob {
              flex: 1 0 auto;
              display: flex;
              flex-flow: column nowrap;
              justify-content: flex-end;
              align-items: flex-start;

              .social{
                display: flex;
                flex-flow: row nowrap;
                justify-content: flex-start;
                align-items: flex-start;
                margin-top: 16px;
                a{
                  display: block;
                  width: 48px;
                  height: 48px;
                  margin-right: 16px;
                  background-color: transparent;
                  background-repeat: no-repeat;
                  background-position: 0 0;
                  background-size: contain;
                  &:last-child{
                    margin-right: 0;
                  }
                  &.social__tg {
                    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='48' height='48' viewBox='0 0 48 48' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M30.9601 19.5469L28.8474 29.3058C28.688 29.9946 28.2724 30.166 27.6817 29.8415L24.4627 27.5181L22.9094 28.9813C22.7375 29.1497 22.5937 29.2905 22.2625 29.2905L22.4937 26.0794L28.4599 20.7989C28.7193 20.5724 28.4036 20.4469 28.0567 20.6734L20.6811 25.2223L17.5058 24.2488C16.8151 24.0376 16.8026 23.5723 17.6495 23.2478L30.0694 18.5612C30.6445 18.35 31.1476 18.6867 30.9601 19.5469Z' fill='%23424242'/%3E%3Crect x='0.5' y='0.5' width='47' height='47' rx='9.5' stroke='%23424242'/%3E%3C/svg%3E");
                  }
                  &.social__vk {
                    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='48' height='48' viewBox='0 0 48 48' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M24.5096 28.4953C25.0089 28.4953 25.0089 27.7752 25.0089 27.4959C25.0089 26.9961 25.5082 26.4964 26.0075 26.4964C26.5068 26.4964 27.3632 27.3534 28.0048 27.9956C28.5041 28.4953 28.5041 28.4953 29.0034 28.4953C29.5027 28.4953 30.5014 28.4953 30.5014 28.4953C30.5014 28.4953 31.5 28.4574 31.5 27.4959C31.5 27.1835 31.158 26.6538 30.0021 25.4969C29.0034 24.4974 28.4911 25.0136 30.0021 22.9982C30.9223 21.7708 31.5919 20.6589 31.477 20.3381C31.3672 20.0317 28.8077 19.544 28.5041 19.9997C27.5055 21.499 27.3222 21.8663 27.0062 22.4984C26.5068 23.4979 26.4574 23.9977 26.0075 23.9977C25.5537 23.9977 25.5082 23.0277 25.5082 22.4984C25.5082 20.8463 25.7474 19.6779 25.0089 19.5C25.0089 19.5 24.0103 19.5 23.511 19.5C22.7071 19.5 22.013 19.9997 22.013 19.9997C22.013 19.9997 21.3934 20.4835 21.5137 20.4995C21.6625 20.5195 22.5123 20.2926 22.5123 20.9992C22.5123 21.499 22.5123 21.9987 22.5123 21.9987C22.5123 21.9987 22.5168 23.9977 22.013 23.9977C21.5137 23.9977 20.5151 21.9987 19.5164 20.4995C19.1245 19.9113 19.0171 19.9997 18.5178 19.9997C17.9825 19.9997 17.5197 20.0207 17.0199 20.0207C16.5205 20.0207 16.4566 20.3391 16.5205 20.4995C17.5192 22.9982 18.2367 24.5674 20.1341 26.5443C21.8742 28.3579 23.0366 28.4544 24.0103 28.4953C24.2599 28.5058 24.0103 28.4953 24.5096 28.4953Z' fill='%23424242'/%3E%3Crect x='0.5' y='0.5' width='47' height='47' rx='9.5' stroke='%23424242'/%3E%3C/svg%3E");
                  }
                  &.social__ok {
                    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='48' height='48' viewBox='0 0 48 48' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M26.9255 26.13C26.1693 26.6056 25.1288 26.7942 24.4416 26.8653L25.0184 27.4284L27.1242 29.5141C27.8943 30.2959 26.6413 31.5177 25.8629 30.7633C25.3358 30.233 24.563 29.4676 23.7571 28.6694L21.6512 30.7606C20.8729 31.5123 19.6199 30.2795 20.3982 29.5113C20.9336 28.981 21.6981 28.2156 22.5041 27.4257L23.0726 26.8626C22.3937 26.7915 21.3366 26.6138 20.5721 26.1272C19.6723 25.5532 19.2776 25.217 19.6254 24.5145C19.8296 24.1154 20.3899 23.7791 21.1323 24.3586C21.1323 24.3586 22.1342 25.1486 23.7516 25.1486C25.3689 25.1486 26.3708 24.3586 26.3708 24.3586C27.1132 23.7819 27.6707 24.1154 27.8777 24.5145C28.22 25.217 27.8253 25.5532 26.9255 26.13ZM20.1691 20.5454C20.1691 18.5854 21.7782 17 23.7488 17C25.7194 17 27.3285 18.5854 27.3285 20.5454C27.3285 22.4971 25.7194 24.0826 23.7488 24.0826C21.7782 24.0826 20.1691 22.4971 20.1691 20.5454ZM21.9907 20.5454C21.9907 21.5048 22.7801 22.2866 23.7488 22.2866C24.7176 22.2866 25.5069 21.5048 25.5069 20.5454C25.5069 19.5777 24.7176 18.7959 23.7488 18.7959C22.7801 18.7959 21.9907 19.5777 21.9907 20.5454Z' fill='%23424242'/%3E%3Crect x='0.5' y='0.5' width='47' height='47' rx='9.5' stroke='%23424242'/%3E%3C/svg%3E");
                  }
                }
              }
            }
          }
          a{
            font-size: 16px;
            white-space: nowrap;
            @include mob {
              font-size: 18px;
              line-height: 28px;
              font-weight: 500;
              white-space: pre-wrap;
            }
          }
          ul{
            position: absolute;
            top: calc(100% + 8px);
            left: -16px;
            display: block;
            padding: 16px;
            border-radius: 0 10px 10px 10px;
            background: var(--color-white);
            opacity: 0;
            visibility: hidden;
            li{
              padding: 0 0 16px;
              &:last-child{
                padding: 0;
              }
              &:before{
                display: none;
              }
              a{
                font-size: 14px;
                font-weight: 500;
                @include mob {
                  font-size: 18px;
                  line-height: 28px;
                  font-weight: 500;
                }
                &:after{
                  display: none!important;
                }
              }
            }
          }
        }
      }
      &.info{
        column-gap: 40px;
        @include mob {
          column-gap: 0;
        }
        li{
          button{
            &.search{
              width: 28px;
              height: 28px;
              @include mob {
                width: 24px;
                height: 24px;
              }
              &:hover{
                svg{
                  use{
                    stroke: var(--color-text-hover);
                  }
                }
              }
              svg{
                use{
                  stroke: var(--color-text-main);
                  stroke-width: 1.5;
                }
              }
            }
            &.lang{
              font-size: 14px;
              color: var(--color-text-gray);
              &:hover{
                &:hover{
                  color: var(--color-text-main);
                  &:after{
                    border-top-color: var(--color-text-main);
                  }
                }
              }
              &:after{
                display: block;
                width: 0; 
                height: 0;
                margin: 0 0 0 4px;
                border-left: 3px solid var(--color-transparent);
                border-right: 3px solid var(--color-transparent);
                border-top: 4px solid var(--color-text-gray);
                content: "";
              }
            }
          }
          .phone{
            @include mob {
              display: none;
            }
          }
        }
      }
    }
  }
}

header.main{
  .burger{
    display: none;
    @include mob {
      display: block;
      .burger__btn{
        position: relative;
        width: 42px;
        height: 42px;
        margin-left: 19px;
        border-radius: 21px;
        border: 1px solid var(--color-text-hover);
        span{
          position: absolute;
          left: 13px;
          width: 14px;
          height: 1px;
          background: var(--color-text-hover);
          transition: all 0.2s linear;
          &:first-child{
            top: 18px;
          }
          &:last-child{
            bottom: 17px;
          }
        }
      }
    }
  }
}
header.main.mob-active{
  a.logo{
    @include mob {
      img{
        width: 40px;
        height: 40px;
      }
    }
  }
  .burger{
    @include mob {
      .burger__btn{
        span{
          left: 12px;
          width: 16px;
          &:first-child{
            top: 20px;
            transform: rotate(45deg);
          }
          &:last-child{
            bottom: 19px;
            transform: rotate(-45deg);
          }
        }
      }
    }
  }
  .wrap{
    ul{
      &.nav{
        @include mob {
          transform: translateX(0);
        }
      }
    }
  }
}


header.main .nav .sub::before{
  position: absolute;
  top: -8px;
  left: -16px;
  display: block;
  width: calc(100% + 32px);
  height: calc(100% + 16px);
  border-radius: 10px 10px 0 0;
  background: var(--color-white);
  opacity: 0;
  visibility: hidden;
  content: '';
}
header.main .nav .sub:hover::before{
  opacity: 1;
  visibility: visible;
  @include mob {
    opacity: 0;
    visibility: hidden;
  }
}
header.main .nav .sub:hover ul{
  opacity: 1;
  visibility: visible;
  @include mob {
    opacity: 0;
    visibility: hidden;
  }
}
header.main .nav .sub a{
  position: relative;
  display: flex;
  align-items: center;
}
header.main .nav .sub a *{
  z-index: 2;
}
header.main .nav .sub a::after{
  content: '';
  position: relative;
  display: block;
  width: 10px;
  height: 6px;
  margin: 0 0 0 8px;
  background: url("data:image/svg+xml,%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 0.584961L5 4.58496L1 0.584961' stroke='%23121720'/%3E%3C/svg%3E%0A") no-repeat;
  @include mob {
    margin-left: 12px;
    transform: rotate(-90deg);
  }
}
header.main .nav .sub a:hover::after{
  transform: scale(-1);
  @include mob {
    transform: rotate(-90deg);
  } 
}


header.main .wrap ul.nav li.sub ul{
  @include mob {
    position: static;
    display: none;
    padding: 20px 0 0;
    background: transparent;
    opacity: 1;
    visibility: visible;
  }
}
header.main .wrap ul.nav li ul li{
  @include mob {
    padding: 0;
  }
}
header.main .wrap ul.nav li.sub.js-show ul{
  @include mob {
    display: block;
  }
}
header.main .wrap ul.nav.js-hide > li{
  @include mob {
    display: none;
  }
}
header.main .wrap ul.nav.js-hide li.sub.js-show,
header.main .wrap ul.nav.js-hide li.sub.js-show li{
  @include mob {
    display: block;
  } 
}
header.main .wrap ul.nav li.sub{
  @include mob {
    padding-left: 0;
  /* transition: padding 0.2s linear; */
  }
}
header.main .wrap ul.nav.js-hide li.sub{
  @include mob {
    padding: 0 38px;
    /* width: calc(100vw - 32px); */
  }
}
header.main .nav.js-hide .sub a:hover::after{
  @include mob {
    transform: rotate(90deg);
    position: absolute;
    left: -22px;
    margin-left: 0;
  } 
}
header.main .wrap ul.nav.js-hide li.mob-only{
  @include mob {
    display: flex;
  } 
}